import React from 'react';
import QueryString from 'query-string';
import urlencode from 'urlencode';
import Constants from './constants';
import Spinner from './assets/spinner.svg';

const IFRAME_EVENT_NAME = "IFRAME_MESSAGE";
const IFRAME_LOADED_MESSAGE = "IFRAME_LOADED";

class DharmaLoanFrame extends React.Component {
  constructor(props) {
    super();

    this.state = { authToken: null, loading: true };
    this.constants = Constants[props.partner];

    this.eventListenerActive = false;
    this.onReceiveMessage = this.onReceiveMessage.bind(this);
  }

  componentDidMount() {
    window.addEventListener('message', this.onReceiveMessage);
    this.eventListenerActive = true;
  }

  componentWillUnmount() {
    if (this.eventListenerActive) {
      window.removeEventListener('message', this.onReceiveMessage);
    }
  }

  onReceiveMessage(message) {
    const {AUTH_ROOT} = this.constants;

    if (message.origin === AUTH_ROOT && message.data === IFRAME_LOADED_MESSAGE) {
      window.removeEventListener('message', this.onReceiveMessage);
      this.eventListenerActive = false;
      this.setState((prevState, props) => {
        prevState.loading = false;
        return prevState;
      })
    }
  }

  static getAuthToken() {
    const authToken = QueryString.parse(location.search).dharmaAuthToken;
    return authToken;
  }

  static isAuthTokenPresent() {
    const authToken = DharmaLoanFrame.getAuthToken();
    return (typeof authToken !== 'undefined');
  }

  render() {
    const {AUTH_ROOT, SCREEN_WIDTHS} = this.constants;

    const authToken = DharmaLoanFrame.getAuthToken();
    let src = AUTH_ROOT + '?';

    if (authToken) {
      src += "authToken=" +  urlencode(authToken) + "&";
    }

    if (this.props.env) {
      src += "env=" +  this.props.env + "&";
    }

    src += "screenWidth=" + this.props.screenWidth + "&";

    let width = '600px';

    const isSmallScreen = this.props.screenWidth === SCREEN_WIDTHS.SMALL;
    if (isSmallScreen) {
        width = '100%';
    }

    const loaderStyle = {
      width: 40,
      height: 40,
      position: 'absolute',
      left: '50%',
      top: '50%',
      transform: 'translate(-50%,-50%)',
      display: this.state.loading ? 'inline-block' : 'none'
    }

    return (
      <div style={{ width: '100%', textAlign: 'center', position: 'relative' }}>
        <iframe
          name="dharma-loan-frame"
          src={src}
          width={width}
          height="550px"
          frameBorder="0" />
        <Spinner style={loaderStyle} />
      </div>
    );
  }
}

export default DharmaLoanFrame;



